<script>
export default {
  name: 'my-timeEffect',
  data() {
    return {
      direction: true,
      timer: null,
      lists: [],
      list: ['自由', 'Happy', 'Manly', '航海', '晚灯', '漫画', '赛车']
    }
  },
  methods: {
    show() {
      function clickEffect() {
        const balls = []
        let longPressed = false
        let longPress
        let multiplier = 0
        let width, height
        let origin
        let normal
        let ctx
        const colours = ['#F73859', '#14FFEC', '#00E0FF', '#FF99FE', '#FAF15D']
        const canvas = document.createElement('canvas')
        document.getElementsByClassName('container')[0].appendChild(canvas)
        canvas.setAttribute('style', 'width: 100%; height: 100%; top: 0; left: 0; z-index: 99999; position: fixed; pointer-events: none;')
        const pointer = document.createElement('span')
        pointer.classList.add('pointer')
        document.getElementsByClassName('container')[0].appendChild(pointer)

        if (canvas.getContext && window.addEventListener) {
          ctx = canvas.getContext('2d')
          updateSize()
          window.addEventListener('resize', updateSize, false)
          loop()
          window.addEventListener(
            'mousedown',
            function (e) {
              pushBalls(randBetween(10, 20), e.clientX, e.clientY)
              document.body.classList.add('is-pressed')
              longPress = setTimeout(function () {
                document.body.classList.add('is-longpress')
                longPressed = true
              }, 500)
            },
            false
          )
          window.addEventListener(
            'mouseup',
            function (e) {
              clearInterval(longPress)
              if (longPressed === true) {
                document.body.classList.remove('is-longpress')
                pushBalls(randBetween(50 + Math.ceil(multiplier), 100 + Math.ceil(multiplier)), e.clientX, e.clientY)
                longPressed = false
              }
              document.body.classList.remove('is-pressed')
            },
            false
          )
          window.addEventListener(
            'mousemove',
            function (e) {
              const x = e.clientX
              const y = e.clientY
              pointer.style.top = y + 'px'
              pointer.style.left = x + 'px'
            },
            false
          )
        } else {
          console.log('canvas or addEventListener is unsupported!')
        }

        function updateSize() {
          canvas.width = window.innerWidth * 2
          canvas.height = window.innerHeight * 2
          canvas.style.width = window.innerWidth + 'px'
          canvas.style.height = window.innerHeight + 'px'
          ctx.scale(2, 2)
          width = canvas.width = window.innerWidth
          height = canvas.height = window.innerHeight
          origin = {
            x: width / 2,
            y: height / 2
          }
          normal = {
            x: width / 2,
            y: height / 2
          }
        }
        class Ball {
          constructor(x = origin.x, y = origin.y) {
            this.x = x
            this.y = y
            this.angle = Math.PI * 2 * Math.random()
            if (longPressed === true) {
              this.multiplier = randBetween(14 + multiplier, 15 + multiplier)
            } else {
              this.multiplier = randBetween(6, 12)
            }
            this.vx = (this.multiplier + Math.random() * 0.5) * Math.cos(this.angle)
            this.vy = (this.multiplier + Math.random() * 0.5) * Math.sin(this.angle)
            this.r = randBetween(8, 12) + 3 * Math.random()
            this.color = colours[Math.floor(Math.random() * colours.length)]
          }

          update() {
            this.x += this.vx - normal.x
            this.y += this.vy - normal.y
            normal.x = (-2 / window.innerWidth) * Math.sin(this.angle)
            normal.y = (-2 / window.innerHeight) * Math.cos(this.angle)
            this.r -= 0.3
            this.vx *= 0.9
            this.vy *= 0.9
          }
        }

        function pushBalls(count = 1, x = origin.x, y = origin.y) {
          for (let i = 0; i < count; i++) {
            balls.push(new Ball(x, y))
          }
        }

        function randBetween(min, max) {
          return Math.floor(Math.random() * max) + min
        }

        function loop() {
          ctx.fillStyle = 'rgba(255, 255, 255, 0)'
          ctx.clearRect(0, 0, canvas.width, canvas.height)
          for (let i = 0; i < balls.length; i++) {
            const b = balls[i]
            if (b.r < 0) continue
            ctx.fillStyle = b.color
            ctx.beginPath()
            ctx.arc(b.x, b.y, b.r, 0, Math.PI * 2, false)
            ctx.fill()
            b.update()
          }
          if (longPressed === true) {
            multiplier += 0.2
          } else if (!longPressed && multiplier >= 0) {
            multiplier -= 0.4
          }
          removeBall()
          requestAnimationFrame(loop)
        }

        function removeBall() {
          for (let i = 0; i < balls.length; i++) {
            const b = balls[i]
            if (b.x + b.r < 0 || b.x - b.r > width || b.y + b.r < 0 || b.y - b.r > height || b.r < 0) {
              balls.splice(i, 1)
            }
          }
        }
      }
      clickEffect() // 调用特效函数
    },
    btn1() {
      this.direction = false
      this.rotation()
    },
    btn2() {
      this.direction = true
      this.rotation()
    },
    rotation() {
      const lists = document.querySelectorAll('.item')
      // const lists = this.$refs.item
      // console.log(this.$refs.item === document.querySelectorAll('.item')) 为什么为false?
      // console.log(lists)
      if (this.direction) {
        this.$refs.slide.appendChild(lists[0])
        // document.getElementsByClassName('slide')[0].appendChild(lists[0])
      } else {
        this.$refs.slide.prepend(lists[lists.length - 1])
        // document.getElementsByClassName('slide')[0].prepend(lists[lists.length - 1])
      }
    },
    // 鼠标经过-停止定时器
    mouseover() {
      document.querySelector('.container').addEventListener('mouseover', () => {
        clearInterval(this.timer)
      })
    },
    // 鼠标离开-开启定时器
    mouseout() {
      document.querySelector('.container').addEventListener('mouseout', () => {
        this.timer = setInterval(this.rotation, 5000)
      })
    },
    world() {
      const container = document.querySelector('.world')
      const panels = document.querySelectorAll('.panel')

      container.addEventListener('click', function (e) {
        // console.log(e)
        let clickedPanel = null
        if (e.target.tagName === 'H2') {
          clickedPanel = e.target.parentNode
        } else {
          clickedPanel = e.target
        }
        panels.forEach(panel => {
          panel.classList.remove('active')
        })
        clickedPanel.classList.add('active')
      })
    }
  },
  mounted() {
    this.show()
    this.timer = setInterval(this.rotation, 5000)
    // 离开当前页面时销毁定时器
    this.$once('hook:beforeDestroy', () => {
      clearInterval(this.timer)
      this.timer = null
    })
    this.mouseover()
    this.mouseout()
    this.world()
  }
}
</script>

<style lang="less">
.el-main {
  overflow-x: hidden;
  height: 610px;
}
.box-card {
  width: 100%;
  background-color: rgba(242, 242, 242, 0.6);
  margin-bottom: 10px;
}
.clearfix span {
  font-size: 16px;
  font-weight: 700;
}
.container {
  width: 90%;
  height: 450px;
  padding: 25px 50px;
  box-shadow: 0 30px 50px #dbdbdb;
  background-color: rgba(242, 242, 242, 0.6);
  position: relative;
}
#slide {
  width: max-content;
  margin-top: 40px;
}
.item {
  width: 150px;
  height: 200px;
  background-size: cover;
  background-position: 40%, 100%;
  position: absolute;
  display: inline-block;
  border-radius: 20px;
  box-shadow: 0 30px 50px #505050;
  transition: 1s;
}
.item:nth-child(1),
.item:nth-child(2) {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 0px;
  box-shadow: none;
}
.item:nth-child(3) {
  left: 58%;
}
.item:nth-child(4) {
  left: calc(58% + 170px);
}
.item:nth-child(5) {
  left: calc(58% + 340px);
}
.item:nth-child(n + 6) {
  left: calc(58% + 490px);
  opacity: 0;
}
.item .content {
  width: 200px;
  position: absolute;
  left: 100px;
  top: 50%;
  transform: translateY(-50%);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #eee;
  display: none;
}
.item:nth-child(2) .content {
  display: block;
}
.item .name {
  font-size: 20px;
  font-weight: 700;
  opacity: 0;
  animation: showcontent 1s ease-in-out 1 forwards;
}
.item .des {
  margin: 10px 0;
  opacity: 0;
  animation: showcontent 1s ease-in-out 0.3s 1 forwards;
}
.item button {
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  opacity: 0;
  animation: showcontent 1s ease-in-out 0.6s 1 forwards;
  color: #9eb0ff;
  font-weight: 700;
}
@keyframes showcontent {
  from {
    opacity: 0;
    transform: translateY(100px);
    // 模糊度
    filter: blur(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
    filter: blur(0);
  }
}
.buttons {
  width: 90%;
  position: absolute;
  bottom: 20px;
  text-align: center;
}
.el-button {
  margin: 0 40px;
  transition: 0.5s;
}
.el-button:focus,
.el-button:hover {
  color: #c7d3ff;
  border-color: #dcc7ff;
  background-color: #ecf5ff;
}
// 百叶窗效果css
.world {
  display: flex;
  width: 70vw;
}

.panel {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 40vh;
  border-radius: 8px;
  cursor: pointer;
  flex: 0.5;
  color: #fff;
  margin: 10px;
  position: relative;
  transition: flex 0.7s ease-in;
}

.panel:first-of-type {
  background-position: 50% 30%;
}

.panel:nth-of-type(3) {
  background-position: 80% 10%;
}

.panel:nth-of-type(5) {
  background-position: 50% 10%;
}

.panel h2 {
  position: absolute;
  font-size: 24px;
  bottom: 20px;
  left: 20px;
  opacity: 0;
}

.panel.active {
  flex: 5;
}

.panel.active h2 {
  opacity: 1;
  transition: opacity 0.3s ease-in 0.4s;
}

@media (max-width: 480px) {
  .container {
    width: 100vw;
    padding: 0 5px;
  }

  .panel {
    margin: 0 5px;
  }

  .panel.active {
    flex: 4;
  }

  .panel:nth-of-type(3) {
    background-position: 65% 10%;
  }

  .panel:nth-of-type(4),
  .panel:nth-of-type(5) {
    display: none;
  }
}
